import { Component, EventEmitter, Output } from '@angular/core';
import { SailsClient } from 'ngx-sails';
import { AuthService } from '../auth.service';
import { Post } from '../post-comment';

@Component({
  selector: 'app-post-form',
  templateUrl: './post-form.component.html',
  styleUrls: ['./post-form.component.scss']
})
export class PostFormComponent {

  @Output() posted = new EventEmitter<Post>();
  newPost: string;

  constructor(
    private sails: SailsClient,
    private authService: AuthService
  ) { }

  post(): void {
    if (this.newPost && this.newPost.replace(/\s/g, '').length) {
      const post = {
        message: this.newPost,
        user: this.authService.getUserId()
      }
      this.sails.post('/post', post).subscribe((resp) => {
        console.log(resp);
        //test if successful and emit post to timeline
        this.posted.emit(resp.data);
      });
      console.log(`Post: \n"${this.newPost}"`);
      this.newPost = undefined;
    }
  }

}
