import { Component, OnInit, OnDestroy } from '@angular/core';
import { SailsClient } from 'ngx-sails';
import { Post } from '../post-comment';

@Component({
  selector: 'app-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.scss']
})
export class TimelineComponent implements OnInit, OnDestroy {

  posts = [];

  constructor(private sails: SailsClient) { } // posts fetch service to populate timeline

  connection;

  onPosted(post: Post) {
    console.log('posted');
    this.posts.unshift(post);
  }

  ngOnInit() {
    // add to service be more concise
    this.connection = this.sails.on('post').subscribe(resp => {
      console.log('post event!', resp);
      switch(resp.verb) {
        case 'created':
          this.posts.unshift(resp.data);
          break;
        default:
          // unrecognised action error
          console.log('unrecognised post event');
      }
    });

    this.sails.get('/post').subscribe(resp => {
      console.log('get chat', resp);
      this.posts = resp.data.reverse();
    });
  }

  ngOnDestroy() {
    this.connection.unsubscribe();
  }

}
